import React, { useState, useEffect } from "react";
// import { useHistory } from "react-router-dom";
import PropTypes from "prop-types";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import SaveIcon from "@material-ui/icons/Save";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import CircularProgress from "@material-ui/core/CircularProgress";

// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import customInputStyle from "assets/jss/material-dashboard-react/components/customInputStyle.js";

// date time picker
import {
  MuiPickersUtilsProvider,
  DatePicker,
  TimePicker,
} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";

// constants
import { MAJORS } from "./MAJORS";
import { USA_INDUSTRIES } from "./USA_INDUSTRIES";
import { ACADEMIC_DEPARTMENTS } from "./ACADEMIC_DEPARTMENTS";

// custom classes
import globalState from "../../globalState";
import { useAuthorizedApis } from "../../apis";
import extractErrorMessageFromResponse from "../../utils/extractErrorMessageFromResponse";
import { studentRoutes } from "../../routes";

const styles = {
  ...customInputStyle,
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};

const useStyles = makeStyles(styles);

export default function Profile(props) {
  const { setLayoutRoutes } = props; // function from studentLayout
  // const history = useHistory();
  const classes = useStyles();
  const { post, get, response } = useAuthorizedApis();

  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");
  const [snackbarSeverity, setSnackbarSeverity] = useState("error"); // error, info, success, warning
  const showErrorSnackbar = (message) => {
    setSnackbarSeverity("error");
    setSnackbarMessage(message);
    setSnackbarOpen(true);
  };
  const showSuccessSnackbar = (message) => {
    setSnackbarSeverity("success");
    setSnackbarMessage(message);
    setSnackbarOpen(true);
  };
  const handleSnackbarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setSnackbarOpen(false);
  };
  const Alert = (props) => (
    <MuiAlert elevation={6} variant="filled" {...props} />
  );

  const [profile, setProfile] = useState({
    firstName: "",
    middleName: "",
    lastName: "",
    gender: "",
    email: "",
    affiliationStatus: "",
    academicDepartment: [], // string[]
    major: [], // string[]
    ideaName: "",
    ideaDescription: "",
    usaIndustries: [], // string[]
    program: "",
    meetingPlatform: "",
    meetingDate: new Date(),
    meetingTime: new Date(),
    notes: "",
  });
  const [loading, setLoading] = useState(true);

  const loadUserProfile = async () => {
    const responseJson = await get("/students/me/profile");
    if (response.ok) {
      setProfile({
        ...profile,
        firstName: responseJson.first_name || "",
        middleName: responseJson.middle_name || "",
        lastName: responseJson.last_name || "",
        gender: responseJson.gender || "",
        email: responseJson.email || "",
        affiliationStatus: responseJson.uci_affiliation || "",
        academicDepartment: Array.isArray(responseJson.academic_department)
          ? responseJson.academic_department
          : [],
        major: Array.isArray(responseJson.major) ? responseJson.major : [],
        ideaName: responseJson.idea_name || "",
        usaIndustries: Array.isArray(responseJson.usa_industries)
          ? responseJson.usa_industries
          : [],
        ideaDescription: responseJson.idea_description || "",
        program: responseJson.program || "",
        meetingDate: responseJson.meeting_date // UTC
          ? new Date(responseJson.meeting_date)
          : new Date(),
        meetingTime: responseJson.meeting_time // UTC
          ? new Date(responseJson.meeting_time)
          : new Date(),
        meetingPlatform: responseJson.meeting_platform || "",
        notes: responseJson.notes || "",
      });
    } else {
      // backend returns empty profile when it is not created
      // the empty profile does not actually exist and only contains email
      // the other fields are all null
      showErrorSnackbar(await extractErrorMessageFromResponse(response));
    }
  };

  const saveUserProfile = async () => {
    await post("/students/me/profile", {
      first_name: profile.firstName,
      middle_name: profile.middleName,
      last_name: profile.lastName,
      gender: profile.gender,
      uci_affiliation: profile.affiliationStatus,
      academic_department: profile.academicDepartment,
      major: profile.major,
      idea_name: profile.ideaName,
      usa_industries: profile.usaIndustries,
      idea_description: profile.ideaDescription,
      program: profile.program,
      meeting_date: profile.meetingDate, // Date.toString()
      // actual: "yyyy-mm-dd" in UTC
      meeting_time: [profile.meetingTime], // TODO allow multiple
      // actual: "yyyy-mm-ddThh:mm:ss.sssZ" in UTC
      meeting_platform: profile.meetingPlatform,
      notes: profile.notes,
    });

    if (response.ok) {
      if (!globalState.userHasCompletedProfile) {
        globalState.userHasCompletedProfile = true;
        setLayoutRoutes(studentRoutes); // function from studentLayout
      }
      showSuccessSnackbar("Profile saved.");
      // showSuccessSnackbar("Profile saved. Going to dashboard in 3 sec...");
      // setTimeout(() => history.replace("/student/dashboard"), 3000);
    } else {
      showErrorSnackbar(await extractErrorMessageFromResponse(response));
    }
  };

  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    setProfile({
      ...profile,
      [name]: value,
    });
  };

  const handleSave = () => {
    if (isDataValid()) {
      saveUserProfile();
    }
  };

  const isDataValid = () => {
    if (profile.firstName.length === 0) {
      showErrorSnackbar("Please enter your first name.");
      return false;
    }

    if (profile.lastName.length === 0) {
      showErrorSnackbar("Please enter your last name.");
      return false;
    }

    if (profile.gender.length === 0) {
      showErrorSnackbar("Please select your gender.");
      return false;
    }

    if (profile.affiliationStatus.length === 0) {
      showErrorSnackbar("Please select your UCI affiliation.");
      return false;
    }

    if (profile.academicDepartment.length === 0) {
      showErrorSnackbar("Please select your academic department.");
      return false;
    }

    if (profile.major.length === 0) {
      showErrorSnackbar("Please select your major.");
      return false;
    }

    if (profile.ideaName.length === 0) {
      showErrorSnackbar("Please enter your idea name.");
      return false;
    }

    if (profile.usaIndustries.length === 0) {
      showErrorSnackbar("Please select your USA industries.");
      return false;
    }

    if (profile.ideaDescription.length === 0) {
      showErrorSnackbar("Please enter your idea description.");
      return false;
    }

    if (profile.program.length === 0) {
      showErrorSnackbar("Please select your program.");
      return false;
    }

    if (profile.meetingPlatform.length === 0) {
      showErrorSnackbar("Please select your meeting platform.");
      return false;
    }

    return true;
  };

  const Spinner = (
    <div style={{ textAlign: "center" }}>
      <CircularProgress />
    </div>
  );

  const ProfileForm = (
    <div>
      {/* Basic Information */}
      <Card>
        <CardHeader color="primary">
          <h4 className={classes.cardTitleWhite}>Personal Information</h4>
        </CardHeader>
        <CardBody>
          {/* First name, middle name, last name, gender, email */}
          <GridContainer>
            <GridItem xs={12} sm={12} md={2}>
              <CustomInput
                labelText="First Name"
                id="first-name"
                formControlProps={{
                  fullWidth: true,
                  required: true,
                }}
                inputProps={{
                  name: "firstName",
                  onChange: handleChange,
                  value: profile.firstName,
                }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={2}>
              <CustomInput
                labelText="Middle Name"
                id="middle-name"
                formControlProps={{
                  fullWidth: true,
                }}
                inputProps={{
                  name: "middleName",
                  onChange: handleChange,
                  value: profile.middleName,
                }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={2}>
              <CustomInput
                labelText="Last Name"
                id="last-name"
                formControlProps={{
                  fullWidth: true,
                  required: true,
                }}
                inputProps={{
                  name: "lastName",
                  onChange: handleChange,
                  value: profile.lastName,
                }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={2}>
              <FormControl required className={classes.formControl} fullWidth>
                <InputLabel htmlFor="gender-required">Gender</InputLabel>
                <Select
                  value={profile.gender}
                  onChange={handleChange}
                  name="gender"
                  inputProps={{
                    id: "gender-required",
                    value: profile.gender,
                  }}
                >
                  <MenuItem aria-label="None" value="" />
                  <MenuItem value="Male">Male</MenuItem>
                  <MenuItem value="Female">Female</MenuItem>
                  <MenuItem value="Non-Binary">Non-Binary</MenuItem>
                </Select>
                <FormHelperText>Required</FormHelperText>
              </FormControl>
            </GridItem>
            <GridItem xs={12} sm={12} md={4}>
              <CustomInput
                labelText="Email"
                id="email"
                formControlProps={{
                  fullWidth: true,
                }}
                inputProps={{
                  name: "email",
                  // disabled: true,
                  readOnly: true,
                  disableUnderline: true,
                  value: profile.email,
                }}
              />
            </GridItem>
          </GridContainer>
          {/* UCI affiliation, department, major */}
          <GridContainer>
            <GridItem xs={12} sm={12} md={4}>
              {/* Ugly fix to stay aligned with autocomplete */}
              <div style={{ marginTop: 16 }}>
                <FormControl required className={classes.formControl} fullWidth>
                  <InputLabel htmlFor="affiliation-status-required">
                    UCI Affiliation
                  </InputLabel>
                  <Select
                    value={profile.affiliationStatus}
                    onChange={handleChange}
                    name="affiliationStatus"
                    inputProps={{
                      id: "affiliation-status-required",
                      value: profile.affiliationStatus,
                    }}
                  >
                    <MenuItem aria-label="None" value="" />
                    <MenuItem value="N/A">N/A</MenuItem>
                    <MenuItem value="Undergraduate">Undergraduate</MenuItem>
                    <MenuItem value="Graduate">Graduate</MenuItem>
                    <MenuItem value="Alumni">Alumni</MenuItem>
                    <MenuItem value="DCE">DCE</MenuItem>
                  </Select>
                  <FormHelperText style={{ marginTop: 8 }}>
                    Required
                  </FormHelperText>
                </FormControl>
              </div>
            </GridItem>
            <GridItem xs={12} sm={12} md={4}>
              <FormControl required className={classes.formControl} fullWidth>
                <Autocomplete
                  multiple
                  filterSelectedOptions
                  options={ACADEMIC_DEPARTMENTS}
                  id="academicDepartment"
                  value={profile.academicDepartment}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Academic Department"
                      margin="normal"
                    />
                  )}
                  name="academicDepartment"
                  onChange={(event, value) => {
                    // value is string[]
                    if (value) {
                      setProfile({
                        ...profile,
                        academicDepartment: value,
                      });
                    }
                  }}
                />
                <FormHelperText>Required</FormHelperText>
              </FormControl>
            </GridItem>
            <GridItem xs={12} sm={12} md={4}>
              <FormControl required className={classes.formControl} fullWidth>
                {/* .MuiFormControl-marginNormal margin-top:16px */}
                {/* Spent 3 hours but still cannot change the inner form control */}
                <Autocomplete
                  multiple
                  filterSelectedOptions
                  options={MAJORS}
                  // getOptionLabel={(option) => option.title}
                  id="major"
                  value={profile.major}
                  renderInput={(params) => (
                    <TextField {...params} label="Majors" margin="normal" />
                  )}
                  name="major"
                  onChange={(event, value) => {
                    // value is string[]
                    if (value) {
                      setProfile({
                        ...profile,
                        major: value,
                      });
                    }
                  }}
                />
                {/* .MuiFormControl-marginNormal margin-bottom:8px*/}
                <FormHelperText>Required</FormHelperText>
              </FormControl>
            </GridItem>
          </GridContainer>
        </CardBody>
      </Card>

      {/* Advanced Information */}
      <Card>
        <CardHeader color="primary">
          <h4 className={classes.cardTitleWhite}>Advanced Information</h4>
          <p className={classes.cardCategoryWhite}>
            Your project idea and preferences
          </p>
        </CardHeader>
        <CardBody>
          {/* Idea name, industry */}
          <GridContainer>
            <GridItem xs={12} sm={12} md={3}>
              <div style={{ marginTop: 16 }}>
                <CustomInput
                  labelText="Idea/Company Name"
                  id="idea-name"
                  formControlProps={{
                    fullWidth: true,
                    required: true,
                  }}
                  inputProps={{
                    name: "ideaName",
                    onChange: handleChange,
                    value: profile.ideaName,
                  }}
                />
              </div>
            </GridItem>
            <GridItem xs={12} sm={12} md={9}>
              <FormControl required className={classes.formControl} fullWidth>
                {/* .MuiFormControl-marginNormal margin-top:16px */}
                {/* TODO allow multiple choices */}
                {/* https://material-ui.com/components/autocomplete/#multiple-values */}
                <Autocomplete
                  multiple
                  filterSelectedOptions
                  options={USA_INDUSTRIES}
                  id="usa-industries"
                  value={profile.usaIndustries}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="USA Industries"
                      margin="normal"
                    />
                  )}
                  name="usaIndustries"
                  onChange={(event, value) => {
                    // value: string[]
                    if (value) {
                      setProfile({
                        ...profile,
                        usaIndustries: value,
                      });
                    }
                  }}
                />
                {/* .MuiFormControl-marginNormal margin-bottom:8px*/}
                <FormHelperText>Required</FormHelperText>
              </FormControl>
            </GridItem>
          </GridContainer>
          {/* Idea desc */}
          <GridContainer>
            <GridItem xs={12} sm={12} md={12}>
              <CustomInput
                labelText="Idea/Company Description"
                id="idea-description"
                formControlProps={{
                  fullWidth: true,
                  required: true,
                }}
                inputProps={{
                  multiline: true,
                  rows: 3,
                  name: "ideaDescription",
                  onChange: handleChange,
                  value: profile.ideaDescription,
                }}
              />
            </GridItem>
          </GridContainer>
          {/* Program */}
          <GridContainer>
            <GridItem xs={12} sm={12} md={6}>
              <FormControl required className={classes.formControl} fullWidth>
                <InputLabel htmlFor="program-required">
                  Program Selection
                </InputLabel>
                <Select
                  value={profile.program}
                  onChange={handleChange}
                  name="program"
                  inputProps={{
                    id: "program-required",
                    value: profile.program,
                  }}
                >
                  <MenuItem aria-label="None" value="" />
                  <MenuItem value="N/A">N/A</MenuItem>
                  <MenuItem value="E2: Fall Quarter">E2: Fall Quarter</MenuItem>
                  <MenuItem value="E2: Winter Quarter">
                    E2: Winter Quarter
                  </MenuItem>
                  <MenuItem value="E2: Spring Quarter">
                    E2: Spring Quarter
                  </MenuItem>
                  <MenuItem value="E2: Summer Session I">
                    E2: Summer Session I
                  </MenuItem>
                  <MenuItem value="E2: Summer Session II">
                    E2: Summer Session II
                  </MenuItem>
                  <MenuItem value="E2: Self-Paced Program (Virtual)">
                    E2: Self-Paced Program (Virtual)
                  </MenuItem>
                </Select>
                <FormHelperText>Required</FormHelperText>
              </FormControl>
              <FormHelperText>
                Submission Deadline for the E2: Winter Quarter 2021 is on
                Monday, December 28, 2020. <br />
                The Winter Quarter 2021 program will begin on January 11, 2021 -
                February 12, 2021 <br />
                Submission Deadline for the E2: Spring Quarter 2021 is on
                Monday, March 29, 2021. <br />
                The Spring Quarter 2021 program will begin on April 5, 2021 -
                May 7, 2021 <br />
              </FormHelperText>
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <FormControl required className={classes.formControl} fullWidth>
                <InputLabel htmlFor="program-meeting-platform-required">
                  Program Meeting Platform
                </InputLabel>
                <Select
                  value={profile.meetingPlatform}
                  onChange={handleChange}
                  name="meetingPlatform"
                  inputProps={{
                    id: "program-meeting-platform-required",
                    value: profile.meetingPlatform,
                  }}
                >
                  <MenuItem aria-label="None" value="" />
                  <MenuItem value={"N/A"}>N/A</MenuItem>
                  <MenuItem value={"Mobile Phone Call"}>
                    Mobile Phone Call
                  </MenuItem>
                  <MenuItem value={"Zoom Audio/Video Call"}>
                    Zoom Audio/Video Call
                  </MenuItem>
                  <MenuItem value={"UCI Antrepreneur Center"}>
                    UCI Antrepreneur Center
                  </MenuItem>
                  <MenuItem value={"Other Location"}>Other Location</MenuItem>
                </Select>
                <FormHelperText>Required</FormHelperText>
              </FormControl>
            </GridItem>
          </GridContainer>
          {/* Meeting date and time */}
          <GridContainer>
            <GridItem xs={12} sm={12} md={6}>
              <FormControl required className={classes.formControl} fullWidth>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <DatePicker
                    label="Meeting Date"
                    variant="inline"
                    id="meeting-date"
                    value={profile.meetingDate}
                    onChange={(date) => {
                      setProfile({
                        ...profile,
                        meetingDate: date,
                      });
                    }}
                  />
                </MuiPickersUtilsProvider>
                <FormHelperText>Required</FormHelperText>
              </FormControl>
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <FormControl required className={classes.formControl} fullWidth>
                {/* TODO allow multiple choices */}
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <TimePicker
                    label="Meeting Time"
                    variant="inline"
                    id="meeting-time"
                    value={profile.meetingTime}
                    onChange={(date) => {
                      setProfile({
                        ...profile,
                        meetingTime: date,
                      });
                    }}
                  />
                </MuiPickersUtilsProvider>
                <FormHelperText>Required</FormHelperText>
              </FormControl>
            </GridItem>
          </GridContainer>
          {/* Notes */}
          <GridContainer>
            <GridItem xs={12} sm={12} md={12}>
              <CustomInput
                labelText="Notes"
                id="notes"
                formControlProps={{
                  fullWidth: true,
                }}
                inputProps={{
                  multiline: true,
                  rows: 5,
                  name: "notes",
                  onChange: handleChange,
                  value: profile.notes,
                }}
              />
              <FormHelperText>
                Leave any other information you feel is important to know about
                you or your ideas below. If you have nothing else just put N/A.
              </FormHelperText>
            </GridItem>
          </GridContainer>
        </CardBody>
      </Card>

      {/* The button */}
      <GridContainer>
        <GridItem xs={1} sm={1} md={3}></GridItem>
        <GridItem xs={10} sm={10} md={6}>
          <Button
            color="primary"
            round
            onClick={handleSave}
            startIcon={<SaveIcon />}
            fullWidth
          >
            Save
          </Button>
        </GridItem>
        <GridItem xs={1} sm={1} md={3}></GridItem>
      </GridContainer>

      {/* The snack bar */}
      <Snackbar
        autoHideDuration={6000}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
        open={snackbarOpen}
        onClose={handleSnackbarClose}
      >
        <Alert onClose={handleSnackbarClose} severity={snackbarSeverity}>
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </div>
  );

  // componentDidMount
  useEffect(async () => {
    await loadUserProfile();
    setLoading(false);
  }, []);

  return loading ? Spinner : ProfileForm;
}

Profile.propTypes = {
  setLayoutRoutes: PropTypes.func.isRequired, // function from studentLayout
};
