const state = {
  userHasLoggedIn: false,
  userToken: "",
  userRole: "", // "STUDENT" | "STAFF"
  userHasCompletedProfile: true, // default to true for better experience
  logout() {
    this.userHasLoggedIn = false;
    this.userToken = "";
    this.userRole = "";
    this.userHasCompletedProfile = true;
  },
};

export default state;

