import React, { useState, useEffect } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
// creates a beautiful scrollbar
import PerfectScrollbar from "perfect-scrollbar";
import "perfect-scrollbar/css/perfect-scrollbar.css";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
// core components
import Navbar from "components/Navbars/Navbar";
import Footer from "components/Footer/Footer";
import Sidebar from "components/Sidebar/Sidebar";
import Info from "components/Typography/Info";
import HelmetPage from "components/HelmetPage/HelmetPage";
// stylings
import styles from "assets/jss/material-dashboard-react/layouts/adminStyle";
import sidebarBgImage from "assets/img/sidebar-background.jpg";
import logo from "assets/img/logo-uci.jpg";
// misc
import { studentRoutes } from "./routes";
import globalState from "./globalState";
import { useAuthorizedApis } from "./apis";
import Profile from "views/Profile/Profile";

let scrollbar;

const useStyles = makeStyles(styles);

export default function StudentLayout({ ...rest }) {
  const { get } = useAuthorizedApis();
  const [loading, setLoading] = useState(true);
  const { userHasLoggedIn } = globalState;
  const [routes, setRoutes] = useState(studentRoutes);

  // styles
  const classes = useStyles();

  // ref to help us initialize PerfectScrollbar on windows devices
  const mainPanel = React.createRef();
  // states and functions
  const [mobileMode, setMobileMode] = useState(false);
  const toggleDrawer = () => {
    setMobileMode(!mobileMode);
  };
  const turnOffMobileModeIfScreenTooWide = () => {
    if (window.innerWidth >= 960) {
      setMobileMode(false);
    }
  };
  // initialize and destroy the PerfectScrollbar plugin
  useEffect(() => {
    if (userHasLoggedIn) {
      if (navigator.platform.includes("Win")) {
        scrollbar = new PerfectScrollbar(mainPanel.current, {
          suppressScrollX: true,
          suppressScrollY: false,
        });
        document.body.style.overflow = "hidden";
      }
      window.addEventListener("resize", turnOffMobileModeIfScreenTooWide);
      // Specify how to clean up after this effect:
      return function cleanup() {
        if (navigator.platform.includes("Win")) {
          scrollbar.destroy();
        }
        window.removeEventListener("resize", turnOffMobileModeIfScreenTooWide);
      };
    }
  }, [mainPanel]);

  // if user has not completed profile, only dashboard and profile are available
  useEffect(async () => {
    const profile = await get("/students/me/profile");
    const isIdPresent =
      profile && profile.id !== null && profile.id !== undefined;
    globalState.userHasCompletedProfile = isIdPresent;
    if (!isIdPresent) {
      setRoutes(
        studentRoutes.filter(
          (route) => route.path === "/dashboard" || route.path === "/profile"
        )
      );
    }
    setLoading(false);
  }, []);

  const studentUi = (
    <div className={classes.wrapper}>
      {/* sidebar starts*/}
      {loading ? null : (
        <Sidebar
          routes={routes}
          logoText={"ACED"}
          logo={logo}
          image={sidebarBgImage}
          handleDrawerToggle={toggleDrawer}
          open={mobileMode}
          color={"purple"}
          {...rest}
        />
      )}
      {/* sidebar ends*/}

      {/* main panel starts*/}
      <div className={classes.mainPanel} ref={mainPanel}>
        {loading ? (
          <div style={{ textAlign: "center" }}>
            <CircularProgress />
            <Info>loading...</Info>
          </div>
        ) : (
          <React.Fragment>
            <Navbar
              routes={routes}
              handleDrawerToggle={toggleDrawer}
              {...rest}
            />
            <div className={classes.content}>
              <div className={classes.container}>
                <Switch>
                  {routes.map((route, key) =>
                    route.layout === "/student" && route.path === "/profile" ? (
                      <Route
                        path="/student/profile"
                        render={(routeProps) => (
                          <Profile
                            {...routeProps}
                            setLayoutRoutes={setRoutes}
                          />
                        )}
                        key={key}
                      />
                    ) : route.layout === "/student" ? (
                      <Route
                        path={route.layout + route.path}
                        component={route.component}
                        key={key}
                      />
                    ) : null
                  )}
                  <Redirect from="/student" to="/student/dashboard" />
                </Switch>
              </div>
            </div>
            <Footer />
          </React.Fragment>
        )}
      </div>
      {/* main panel ends*/}
    </div>
  );

  return (
    <HelmetPage title="ACED Student">
      {userHasLoggedIn ? studentUi : <Redirect to="/login" />}
    </HelmetPage>
  );
}
