import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Route, Switch, useHistory, useRouteMatch } from "react-router-dom";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Collapse from "@material-ui/core/Collapse";
import IconButton from "@material-ui/core/IconButton";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
// import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
// import Typography from "@material-ui/core/Typography";
// import Paper from "@material-ui/core/Paper";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@material-ui/icons/KeyboardArrowUp";
// import AssignmentLateIcon from "@material-ui/icons/AssignmentLate";
// import AssignmentTurnedInIcon from "@material-ui/icons/AssignmentTurnedIn";
import Button from "@material-ui/core/Button";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import CircularProgress from "@material-ui/core/CircularProgress";

// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";

// custom classes
import ProgramModule from "./ProgramModule";
import ProgramAssignment from "./ProgramAssignment";
import { useAuthorizedApis } from "../../apis";
import extractErrorMessageFromResponse from "../../utils/extractErrorMessageFromResponse";
import globalState from "../../globalState";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF",
    },
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
};

const useStyles = makeStyles(styles);

const useRowStyles = makeStyles({
  root: {
    "& > *": {
      borderBottom: "unset",
    },
  },
});

function ProgramRow(props) {
  const { userRole } = globalState;
  const { program, showSnackbar } = props;
  const [programRowOpen, setRowOpen] = useState(false);
  const [programRowLoading, setRowLoading] = useState(false);
  const isEnrolled =
    program.status === "REGISTERED" || program.status === "DONE";
  const [programEnrolled, setProgramEnrolled] = useState(isEnrolled);
  const EMPTY_MODULES = [createModuleRow("", "Empty", "", "", "")];
  const [programModules, setProgramModules] = useState(EMPTY_MODULES);
  const EMPTY_STUDENTS = [createStudentRow("", "Empty", "", "")];
  const [programStudents, setProgramStudents] = useState(EMPTY_STUDENTS);

  const classes = useRowStyles();
  const history = useHistory();
  const match = useRouteMatch();
  const { get, post, response } = useAuthorizedApis();

  const handleEnrollProgram = async (event) => {
    event.stopPropagation();
    await post(`/students/me/my_programs/${program.id}`);
    if (response.ok) {
      setProgramEnrolled(true);
    } else {
      showSnackbar(await extractErrorMessageFromResponse(response));
    }
  };

  const handleRowExpansion = async () => {
    if (programRowOpen) {
      setRowOpen(false);
    } else {
      setRowOpen(true);
      setRowLoading(true);

      // load program modules
      const responseJsonModules = await get(`/programs/${program.id}/modules`);
      if (response.ok) {
        setProgramModules(
          responseJsonModules.length === 0
            ? EMPTY_MODULES
            : responseJsonModules.map((module) =>
                createModuleRow(
                  module.id,
                  module.name,
                  module.start_date,
                  module.end_date,
                  module.description
                )
              )
        );
      } else {
        setRowOpen(false);
        setRowLoading(false);
        showSnackbar(await extractErrorMessageFromResponse(response));
        return;
      }

      // load registered students if user is staff
      if (userRole === "STAFF") {
        const responseJsonStudents = await get(
          `/staff/me/programs/${program.id}`
        );
        if (response.ok) {
          setProgramStudents(
            responseJsonStudents.length === 0
              ? EMPTY_STUDENTS
              : responseJsonStudents.map((student) =>
                  createStudentRow(
                    student.student_id,
                    student.student_name,
                    student.student_email,
                    student.status
                  )
                )
          );
        } else {
          setRowOpen(false);
          setRowLoading(false);
          showSnackbar(await extractErrorMessageFromResponse(response));
          return;
        }
      }

      setRowLoading(false);
    }
  };

  const spinner = (
    <div style={{ textAlign: "center" }}>
      <CircularProgress />
    </div>
  );

  const firstProgramRow = (
    <TableRow className={classes.root} onClick={handleRowExpansion}>
      <TableCell>
        <IconButton aria-label="expand row" size="small">
          {programRowOpen ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
        </IconButton>
      </TableCell>
      <TableCell component="th" scope="row">
        {program.name}
      </TableCell>
      <TableCell>{program.startDate}</TableCell>
      <TableCell>{program.endDate}</TableCell>
      <TableCell>{program.description}</TableCell>
      {/* TODO post /student/me/programs and see which are registered */}
      {userRole === "STUDENT" ? (
        <TableCell style={{ textAlign: "end" }}>
          {programEnrolled ? (
            "Enrolled"
          ) : (
            <Button
              variant="contained"
              color="primary"
              onClick={handleEnrollProgram}
            >
              Enroll
            </Button>
          )}
        </TableCell>
      ) : null}
    </TableRow>
  );

  const secondProgramRow = (
    <TableRow>
      <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
        <Collapse in={programRowOpen} timeout="auto" unmountOnExit>
          <Box margin={1}>
            {programRowLoading ? (
              spinner
            ) : (
              // Module table
              <Table size="small" aria-label="modules">
                <TableHead>
                  <TableRow>
                    <TableCell>Module Name</TableCell>
                    <TableCell>Start Date</TableCell>
                    <TableCell>End Date</TableCell>
                    <TableCell>Description</TableCell>
                    <TableCell>{/* Submodule entrance */}</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {/* Module info rows */}
                  {programModules.map((module) => (
                    <TableRow
                      className={classes.root}
                      key={module.id}
                      onClick={() => {
                        // for the empty module placeholder
                        if (module.id.length === 0) {
                          return;
                        }
                        history.push(
                          `${match.url}/${program.id}/modules/${module.id}`
                        );
                      }}
                    >
                      <TableCell component="th" scope="row">
                        {module.name}
                      </TableCell>
                      <TableCell>{module.startDate}</TableCell>
                      <TableCell>{module.endDate}</TableCell>
                      <TableCell>{module.description}</TableCell>
                      {/* for the empty module placeholder */}
                      {module.id.length === 0 ? null : (
                        <TableCell style={{ textAlign: "end" }}>
                          <Button variant="text">
                            <ArrowForwardIosIcon />
                          </Button>
                        </TableCell>
                      )}
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            )}
            {!programRowLoading && userRole === "STAFF" ? (
              // Student table
              <Table size="small" aria-label="students">
                <TableHead>
                  <TableRow>
                    <TableCell>Student Name</TableCell>
                    <TableCell>Student Email</TableCell>
                    <TableCell>Status</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {/* Student info rows */}
                  {programStudents.map((student) => (
                    <TableRow className={classes.root} key={student.id}>
                      <TableCell component="th" scope="row">
                        {student.name}
                      </TableCell>
                      <TableCell>{student.email}</TableCell>
                      <TableCell>{student.status}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            ) : null}
          </Box>
        </Collapse>
      </TableCell>
    </TableRow>
  );

  return userRole === "STUDENT" && !program.active ? null : (
    <React.Fragment>
      {firstProgramRow}
      {secondProgramRow}
    </React.Fragment>
  );
}

ProgramRow.propTypes = {
  program: PropTypes.shape({
    id: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    startDate: PropTypes.string.isRequired,
    endDate: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    active: PropTypes.bool.isRequired,
    status: PropTypes.string.isRequired, // NO_STATUS, NOT_REGISTERED, REGISTERED, DONE;
    // modules: PropTypes.arrayOf(
    //   PropTypes.shape({
    //     id: PropTypes.string.isRequired,
    //     name: PropTypes.string.isRequired,
    //     startDate: PropTypes.string.isRequired,
    //     endDate: PropTypes.string.isRequired,
    //     description: PropTypes.string.isRequired,
    //   })
    // ),
  }).isRequired,
  showSnackbar: PropTypes.func.isRequired,
};

function createProgramRow(
  id,
  name,
  startDate,
  endDate,
  description,
  active,
  status
) {
  return {
    id,
    name,
    startDate,
    endDate,
    description,
    active, // if true, it is visible to students
    status, // NO_STATUS, NOT_REGISTERED, REGISTERED, DONE;
  };
}

function createModuleRow(id, name, startDate, endDate, description) {
  return {
    id,
    name,
    startDate,
    endDate,
    description,
  };
}

function createStudentRow(id, name, email, status) {
  return {
    id,
    name,
    email,
    status,
  };
}

export default function Programs() {
  const { userRole } = globalState;
  const classes = useStyles();
  const match = useRouteMatch();
  const { get, response } = useAuthorizedApis();
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");
  const [loaded, setLoaded] = useState(false);
  const [programs, setPrograms] = useState([]);

  const showSnackbar = (message) => {
    setSnackbarMessage(message);
    setSnackbarOpen(true);
  };

  const handleSnackbarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setSnackbarOpen(false);
  };

  const Spinner = () => (
    <div style={{ textAlign: "center" }}>
      <CircularProgress />
    </div>
  );

  const Programs = () => (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <CardHeader color="primary">
            <h4 className={classes.cardTitleWhite}>Programs</h4>
          </CardHeader>
          <CardBody>
            <Table aria-label="collapsible table">
              <TableHead>
                <TableRow>
                  <TableCell>{/* Arrow icon */}</TableCell>
                  <TableCell>Program Name</TableCell>
                  <TableCell>Start Date</TableCell>
                  <TableCell>End Date</TableCell>
                  <TableCell>Description</TableCell>
                  {userRole === "STUDENT" ? (
                    <TableCell>{/* Button */}</TableCell>
                  ) : null}
                </TableRow>
              </TableHead>
              <TableBody>
                {programs.map((program) => (
                  <ProgramRow
                    key={program.id}
                    program={program}
                    showSnackbar={showSnackbar}
                  />
                ))}
              </TableBody>
            </Table>
            {/* The snack bar */}
            <Snackbar
              autoHideDuration={6000}
              anchorOrigin={{ vertical: "top", horizontal: "right" }}
              open={snackbarOpen}
              onClose={handleSnackbarClose}
            >
              <Alert onClose={handleSnackbarClose} severity="error">
                {snackbarMessage}
              </Alert>
            </Snackbar>
          </CardBody>
        </Card>
      </GridItem>
    </GridContainer>
  );

  const loadPrograms = async () => {
    let responseJson;
    if (userRole === "STUDENT") {
      // list all programs with NOT_REGISTERED, REGISTERED, DONE
      responseJson = await get("/students/me/programs");
    } else if (userRole === "STAFF") {
      // list all programs with NO_STATUS
      responseJson = await get("/programs");
    } else {
      showSnackbar("Unknown user role: " + userRole);
      return;
    }
    if (response.ok) {
      setPrograms(
        responseJson.map((program) =>
          createProgramRow(
            program.id,
            program.name,
            program.start_date,
            program.end_date,
            program.description,
            program.active,
            program.status
          )
        )
      );
    } else {
      setPrograms([]);
      showSnackbar(await extractErrorMessageFromResponse(response));
    }
  };

  // componentDidMount
  useEffect(async () => {
    await loadPrograms();
    setLoaded(true);
  }, []);

  return (
    <Switch>
      <Route
        path={`${match.path}/:programId/modules/:moduleId/assignments/:assignmentId`}
        component={ProgramAssignment}
      />

      <Route
        path={`${match.path}/:programId/modules/:moduleId`}
        component={ProgramModule}
      />

      <Route path={match.path} component={loaded ? Programs : Spinner} />
    </Switch>
  );
}
