import useFetch from "use-http";
import globalState from "./globalState";

// const HOST = "https://postman-echo.com";
// const HOST = "https://httpbin.org";
const HOST = "http://localhost:8080/v1";

const GLOBAL_OPTIONS = {
  headers: {
    Accept: "application/json",
    // "Access-Control-Allow-Origin": "*",
  },
  cachePolicy: "no-cache",
};

export const useApis = (options) => {
  if (options === null || options === undefined) {
    return useFetch(HOST, GLOBAL_OPTIONS);
  }

  options.headers.Accept = GLOBAL_OPTIONS.headers.Accept;
  options.cachePolicy = GLOBAL_OPTIONS.cachePolicy;
  // don't use { ...GLOBAL_OPTIONS, ...moreOptions }
  // because moreOptions can overwrite the headers
  return useFetch(HOST, options);
};

export const useAuthorizedApis = () => {
  const { userToken } = globalState;
  if (userToken.length === 0) {
    console.error("useAuthorizedApis: userToken is empty!");
    return useApis();
  }

  return useApis({
    headers: { Authorization: `Bearer ${userToken}` },
  });
};

export default useApis;
