import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { useHistory, useParams, useRouteMatch } from "react-router-dom";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Button from "@material-ui/core/Button";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import CircularProgress from "@material-ui/core/CircularProgress";

// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import Info from "components/Typography/Info";
import Warning from "components/Typography/Warning";
import Danger from "components/Typography/Danger";
import Success from "components/Typography/Success";

// custom classes
import { useAuthorizedApis } from "../../apis";
import extractErrorMessageFromResponse from "../../utils/extractErrorMessageFromResponse";
import globalState from "../../globalState";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF",
    },
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
};

const useStyles = makeStyles(styles);

const useRowStyles = makeStyles({
  root: {
    "& > *": {
      borderBottom: "unset",
    },
  },
});

const formatDate = (date) => {
  var d = new Date(date),
    month = "" + (d.getMonth() + 1),
    day = "" + d.getDate(),
    year = d.getFullYear();

  if (month.length < 2) month = "0" + month;
  if (day.length < 2) day = "0" + day;

  return [year, month, day].join("-");
};
function SubModuleRow(props) {
  const { subModule } = props;
  const classes = useRowStyles();

  return (
    <React.Fragment>
      <TableRow className={classes.root}>
        <TableCell component="th" scope="row">
          {subModule.name}
        </TableCell>
        <TableCell>{subModule.dates}</TableCell>
        <TableCell>{subModule.description}</TableCell>
        <TableCell>
          <a href={subModule.zoomLink} target="_blank" rel="noreferrer">
            {subModule.zoomLink}
          </a>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}

SubModuleRow.propTypes = {
  subModule: PropTypes.shape({
    id: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    dates: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    zoomLink: PropTypes.string.isRequired,
  }).isRequired,
};

function createSubModuleRow(id, name, dates, description, zoomLink) {
  return {
    id,
    name,
    dates,
    description,
    zoomLink,
  };
}

function AssignmentRow(props) {
  const { assignment } = props;
  const { id, name, dueDate, description, status } = assignment;
  const classes = useRowStyles();
  const match = useRouteMatch();
  const history = useHistory();

  return (
    <React.Fragment>
      <TableRow
        className={classes.root}
        onClick={() => history.push(`${match.url}/assignments/${id}`)}
      >
        <TableCell component="th" scope="row">
          {name}
        </TableCell>
        <TableCell>{dueDate}</TableCell>
        <TableCell>{description}</TableCell>
        <TableCell>
          {status === "TODO" ? (
            <Warning>Todo</Warning>
          ) : status === "UNDER_REVIEW" ? (
            <Info>Under Review</Info>
          ) : status === "APPROVED" ? (
            <Success>Approved</Success>
          ) : status === "REJECT" ? (
            <Danger>Rejected</Danger>
          ) : (
            status
          )}
        </TableCell>
        <TableCell style={{ textAlign: "end" }}>
          <Button variant="text">
            <ArrowForwardIosIcon />
          </Button>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}

AssignmentRow.propTypes = {
  assignment: PropTypes.shape({
    id: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    dueDate: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    status: PropTypes.string.isRequired, // "TODO", "UNDER_REVIEW", "APPROVED", "REJECT"
  }).isRequired,
};

function createAssignmentRow(id, name, dueDate, description, status) {
  return {
    id,
    name,
    dueDate,
    description,
    status,
  };
}

function AssignmentSubmissionsRow(props) {
  const {
    assignmentSubmission,
    showSuccessSnackbar,
    showErrorSnackbar,
  } = props;
  const [status, setStatus] = useState(assignmentSubmission.status);
  const classes = useRowStyles();
  const { post, response } = useAuthorizedApis();
  const { programId, moduleId } = useParams();

  const handleStatusChange = async (event) => {
    const newStatus = event.target.value;
    await post(
      `/staff/me/programs/${programId}/modules/${moduleId}/assignments/submissions`,
      {
        submission_id: assignmentSubmission.id,
        status: newStatus,
      }
    );

    if (response.ok) {
      setStatus(newStatus);
      showSuccessSnackbar("Status updated.");
    } else {
      showErrorSnackbar(await extractErrorMessageFromResponse(response));
    }
  };

  return (
    <React.Fragment>
      <TableRow className={classes.root}>
        <TableCell component="th" scope="row">
          {assignmentSubmission.assignmentName}
        </TableCell>
        <TableCell>{assignmentSubmission.studentName}</TableCell>
        <TableCell>
          <a href={assignmentSubmission.link} target="_blank" rel="noreferrer">
            {assignmentSubmission.link}
          </a>
        </TableCell>
        <TableCell>
          <Select
            value={status}
            onChange={handleStatusChange}
            name="status"
            inputProps={{
              id: "status-required",
              value: status,
            }}
          >
            <MenuItem value="UNDER_REVIEW">Under Review</MenuItem>
            <MenuItem value="APPROVED">Approve</MenuItem>
            <MenuItem value="REJECT">Reject</MenuItem>
          </Select>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}

AssignmentSubmissionsRow.propTypes = {
  assignmentSubmission: PropTypes.shape({
    id: PropTypes.string.isRequired,
    assignmentName: PropTypes.string.isRequired,
    studentName: PropTypes.string.isRequired,
    link: PropTypes.string.isRequired,
    status: PropTypes.string.isRequired,
  }).isRequired,
  showSuccessSnackbar: PropTypes.func.isRequired,
  showErrorSnackbar: PropTypes.func.isRequired,
};

function createAssignmentSubmissionRow(
  id,
  assignmentName,
  studentName,
  link,
  status
) {
  return {
    id,
    assignmentName,
    studentName,
    link,
    status,
  };
}

let subModuleRows = [];
let assignmentRows = [];
let assignmentSubmissionsRows = [];

export default function ProgramModule() {
  const { userRole } = globalState; // "STUDENT" | "STAFF"
  const classes = useStyles();
  const { programId, moduleId } = useParams();
  const history = useHistory();

  const { get, response } = useAuthorizedApis();
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");
  const [snackbarSeverity, setSnackbarSeverity] = useState("error"); // error, info, success, warning
  const showErrorSnackbar = (message) => {
    setSnackbarSeverity("error");
    setSnackbarMessage(message);
    setSnackbarOpen(true);
  };
  const showSuccessSnackbar = (message) => {
    setSnackbarSeverity("success");
    setSnackbarMessage(message);
    setSnackbarOpen(true);
  };
  const [loaded, setLoaded] = useState(false);

  const handleSnackbarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setSnackbarOpen(false);
  };

  const loadData = async () => {
    await loadSubmodules();

    if (userRole === "STUDENT") {
      await loadAssignments();
    } else {
      await loadAssignmentSubmissions();
    }
  };

  const loadSubmodules = async () => {
    const responseJson = await get(
      `/programs/${programId}/modules/${moduleId}/submodules`
    );

    if (response.ok) {
      subModuleRows = responseJson.map((submodule) =>
        createSubModuleRow(
          submodule.id || "",
          submodule.name || "",
          formatDate(submodule.meeting_time || ""),
          submodule.description || "",
          submodule.meeting_link || ""
        )
      );
    } else {
      subModuleRows = [];
      showErrorSnackbar(await extractErrorMessageFromResponse(response));
    }
  };

  const loadAssignments = async () => {
    const responseJson = await get(
      `/students/me/programs/${programId}/modules/${moduleId}/assignments`
    );

    if (response.ok) {
      assignmentRows = responseJson.map((assignment) =>
        createAssignmentRow(
          assignment.id || "",
          assignment.name || "",
          formatDate(assignment.deadline_time || ""),
          assignment.description || "",
          assignment.status || ""
        )
      );
    } else {
      assignmentRows = [];
      showErrorSnackbar(await extractErrorMessageFromResponse(response));
    }
  };

  const loadAssignmentSubmissions = async () => {
    const responseJson = await get(
      `/staff/me/programs/${programId}/modules/${moduleId}/assignments/submissions`
    );

    if (response.ok) {
      assignmentSubmissionsRows = responseJson.map((assignmentSubmission) =>
        createAssignmentSubmissionRow(
          assignmentSubmission.submission_id || "",
          assignmentSubmission.assignment_name || "",
          assignmentSubmission.student_name || "",
          assignmentSubmission.submission_link || "",
          assignmentSubmission.status || ""
        )
      );
    } else {
      assignmentSubmissionsRows = [];
      showErrorSnackbar(await extractErrorMessageFromResponse(response));
    }
  };

  // componentDidMount
  useEffect(async () => {
    await loadData();
    setLoaded(true);
  }, []);

  const spinner = (
    <div style={{ textAlign: "center" }}>
      <CircularProgress />
    </div>
  );

  const assignmentsStudentView = (
    <Card>
      <CardHeader color="primary">
        <h4 className={classes.cardTitleWhite}>Assignments</h4>
      </CardHeader>
      <CardBody>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>{/* Assignment name */}</TableCell>
              <TableCell>Due Date</TableCell>
              <TableCell>Description</TableCell>
              <TableCell>Status</TableCell>
              <TableCell>{/* Assignment detail page entrance */}</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {assignmentRows.map((assignment) => (
              <AssignmentRow key={assignment.id} assignment={assignment} />
            ))}
          </TableBody>
        </Table>
      </CardBody>
    </Card>
  );

  const assignmentStaffView = (
    <Card>
      <CardHeader color="primary">
        <h4 className={classes.cardTitleWhite}>Assignment Review</h4>
      </CardHeader>
      <CardBody>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>{/* Assignment name */}</TableCell>
              <TableCell>Student</TableCell>
              <TableCell>Link</TableCell>
              <TableCell>Status</TableCell>
              <TableCell>
                {/* Assignment review submission page entrance */}
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {assignmentSubmissionsRows.map((assignmentSubmission) => (
              <AssignmentSubmissionsRow
                key={assignmentSubmission.id}
                assignmentSubmission={assignmentSubmission}
                showSuccessSnackbar={showSuccessSnackbar}
                showErrorSnackbar={showErrorSnackbar}
              />
            ))}
          </TableBody>
        </Table>
      </CardBody>
    </Card>
  );

  const mainContent = (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        {/* Submodules */}
        <Card>
          <CardHeader color="primary">
            {<h4 className={classes.cardTitleWhite}>{"Submodules"}</h4>}
          </CardHeader>
          <CardBody>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell></TableCell> {/* Submodule name */}
                  <TableCell>Dates</TableCell>
                  <TableCell>Description</TableCell>
                  <TableCell>Zoom Link</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {subModuleRows.map((subModule) => (
                  <SubModuleRow key={subModule.id} subModule={subModule} />
                ))}
              </TableBody>
            </Table>
          </CardBody>
        </Card>
        {/* STUDENT == Assignments, STAFF == AssignmentSubmissions */}
        {userRole === "STUDENT" ? assignmentsStudentView : assignmentStaffView}
        {/* Bottom Button */}
        <GridContainer>
          <GridItem xs="auto" sm="auto" md={10} />
          <GridItem xs={12} sm={12} md={2} style={{ textAlign: "end" }}>
            <Button
              variant="contained"
              color="secondary"
              onClick={() => history.goBack()}
            >
              Back
            </Button>
          </GridItem>
        </GridContainer>
        {/* The snack bar */}
        <Snackbar
          autoHideDuration={6000}
          anchorOrigin={{ vertical: "top", horizontal: "right" }}
          open={snackbarOpen}
          onClose={handleSnackbarClose}
        >
          <Alert onClose={handleSnackbarClose} severity={snackbarSeverity}>
            {snackbarMessage}
          </Alert>
        </Snackbar>
      </GridItem>
    </GridContainer>
  );

  return !loaded ? spinner : mainContent;
}
