import React, { useState } from "react";
import { Link as RouterLink, useHistory } from "react-router-dom";
import * as Yup from "yup";
import { Formik } from "formik";
import {
  Box,
  Button,
  Container,
  Link,
  TextField,
  Typography,
  makeStyles,
} from "@material-ui/core";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import HelmetPage from "components/HelmetPage/HelmetPage";
import globalState from "../../globalState";
import useApis from "../../apis";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    height: "100%",
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3),
  },
}));

const Login = () => {
  const classes = useStyles();
  const history = useHistory();
  const { post, response } = useApis();
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");
  const handleSnackbarClose = () => setSnackbarOpen(false);
  const showSnackbar = (message) => {
    setSnackbarMessage(message);
    setSnackbarOpen(true);
  };
  const handleSubmit = async (values) => {
    const resposneJson = await post("/authentication", {
      username: values.email,
      password: values.password,
    });
    if (!response.ok) {
      // error
      const { status, error } = resposneJson;
      const message =
        status && error
          ? `${status} ${error}. ${
              status === 401
                ? "Email and password don't match."
                : status === 403
                ? "" // Reserved
                : status === 404
                ? "" // Reserved
                : ""
            }`
          : "Unknown error occured.";
      showSnackbar(message);
      return;
    }
    // ok
    globalState.userToken = resposneJson.token;
    globalState.userRole = resposneJson.role;
    globalState.userHasLoggedIn = true;
    // userHasCompletedProfile? see student layout
    const { userRole } = globalState;
    if (userRole === "STUDENT") {
      history.replace("/student/dashboard");
    } else if (userRole === "STAFF") {
      history.replace("/staff/dashboard");
    } else {
      showSnackbar("Unknown user role: " + userRole);
    }
  };

  return (
    <HelmetPage className={classes.root} title="ACED Login">
      <Box
        display="flex"
        flexDirection="column"
        height="100%"
        justifyContent="center"
      >
        <Container maxWidth="sm">
          <Formik
            initialValues={{
              email: "",
              password: "",
            }}
            validationSchema={Yup.object().shape({
              email: Yup.string()
                .email("Must be a valid UCI email")
                .matches(/^[.\w]+@uci\.edu$/, "Must end with @uci.edu")
                .max(255)
                .required("Email is required"),
              password: Yup.string()
                .min(6, "Must contain at least 6 characters")
                .max(255)
                .required("Password is required"),
            })}
            onSubmit={handleSubmit}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
              values,
            }) => (
              <form onSubmit={handleSubmit}>
                <Box mb={3}>
                  <Typography color="textPrimary" variant="h2">
                    Log in
                  </Typography>
                  <Typography
                    color="textSecondary"
                    gutterBottom
                    variant="body2"
                  >
                    Log in to ACED
                  </Typography>
                </Box>
                <TextField
                  error={Boolean(touched.email && errors.email)}
                  fullWidth
                  helperText={touched.email && errors.email}
                  label="Email Address"
                  margin="normal"
                  name="email"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="email"
                  value={values.email}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.password && errors.password)}
                  fullWidth
                  helperText={touched.password && errors.password}
                  label="Password"
                  margin="normal"
                  name="password"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="password"
                  value={values.password}
                  variant="outlined"
                />
                <Box my={2}>
                  <Button
                    color="primary"
                    disabled={isSubmitting}
                    fullWidth
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    Log in
                  </Button>
                </Box>
                <Typography color="textSecondary" variant="body1">
                  Don&apos;t have an account?{" "}
                  <Link component={RouterLink} to="/register" variant="h6">
                    Register
                  </Link>
                </Typography>
              </form>
            )}
          </Formik>
        </Container>
        <Snackbar
          autoHideDuration={6000}
          anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
          open={snackbarOpen}
          onClose={handleSnackbarClose}
        >
          <Alert onClose={handleSnackbarClose} severity="error">
            {snackbarMessage}
          </Alert>
        </Snackbar>
      </Box>
    </HelmetPage>
  );
};

export default Login;
