import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
// https://www.sitepoint.com/react-router-complete-guide/

// core components
import StudentLayout from "./studentLayout";
import StaffLayout from "./staffLayout";
import Login from "views/Auth/Login";
import Register from "views/Auth/Register";

import "assets/css/material-dashboard-react.css?v=1.9.0";

const hist = createBrowserHistory();

ReactDOM.render(
  // The <BrowserRouter> is the more popular of the two because it uses the HTML5 History API
  // to keep your UI in sync with the URL,
  // whereas the <HashRouter> uses the hash portion of the URL (window.location.hash).
  // https://stackoverflow.com/questions/51974369/hashrouter-vs-browserrouter
  <BrowserRouter history={hist}>
    {/* With <Switch>, only the first child <Route> that matches the location gets rendered */}
    <Switch>
      <Route path="/login" component={Login} />
      <Route path="/register" component={Register} />
      <Route path="/student" component={StudentLayout} />
      <Route path="/staff" component={StaffLayout} />
      <Redirect from="/" to="/login" />
    </Switch>
  </BrowserRouter>,
  document.getElementById("root")
);
