import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { useParams, useHistory } from "react-router-dom";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import CardActions from "@material-ui/core/CardActions";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";

// core components
import CustomInput from "components/CustomInput/CustomInput.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";

// custom classes
import { useAuthorizedApis } from "../../apis";
import extractErrorMessageFromResponse from "../../utils/extractErrorMessageFromResponse";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles({
  bullet: {
    display: "inline-block",
    margin: "0 2px",
    transform: "scale(0.8)",
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
});

ProgramAssignment.propTypes = {
  assignment: PropTypes.shape({
    id: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    dueDates: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    link: PropTypes.string.isRequired,
    status: PropTypes.string.isRequired,
    canSubmit: PropTypes.bool.isRequired,
  }).isRequired,
};

function createAssignment(
  id,
  title,
  dueDates,
  description,
  link,
  status,
  canSubmit
) {
  return {
    id,
    title,
    dueDates,
    description,
    link,
    status,
    canSubmit,
  };
}

export default function ProgramAssignment() {
  const history = useHistory();
  const { programId, moduleId, assignmentId } = useParams();
  const classes = useStyles();
  const [assignment, setAssignment] = useState(
    createAssignment("", "", "", "", "", "", true)
  );
  const { post, get, response } = useAuthorizedApis();
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");
  const [snackbarSeverity, setSnackbarSeverity] = useState("error"); // error, info, success, warning
  const showErrorSnackbar = (message) => {
    setSnackbarSeverity("error");
    setSnackbarMessage(message);
    setSnackbarOpen(true);
  };
  const showSuccessSnackbar = (message) => {
    setSnackbarSeverity("success");
    setSnackbarMessage(message);
    setSnackbarOpen(true);
  };
  const [loaded, setLoaded] = useState(false);

  const handleSnackbarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setSnackbarOpen(false);
  };

  const formatDate = (date) => {
    var d = new Date(date),
      month = "" + (d.getMonth() + 1),
      day = "" + d.getDate(),
      year = d.getFullYear();

    if (month.length < 2) month = "0" + month;
    if (day.length < 2) day = "0" + day;

    return [year, month, day].join("-");
  };

  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    setAssignment({
      ...assignment,
      [name]: value,
    });
  };

  const handleSubmit = async () => {
    await post(
      `/student/me/programs/${programId}/modules/${moduleId}/assignments/${assignmentId}/submissions`,
      {
        submission_link: assignment.link,
      }
    );

    if (response.ok) {
      showSuccessSnackbar("Submitted successfully.");
      setTimeout(goBack, 1000);
    } else {
      showErrorSnackbar(await extractErrorMessageFromResponse(response));
    }
  };

  const loadAssignment = async () => {
    const responseJson = await get(
      `/programs/${programId}/modules/${moduleId}/assignments/${assignmentId}`
    );

    if (response.ok) {
      // setAssignment({
      //   ...assignment,
      //   id: responseJson.id,
      //   title: responseJson.name,
      //   dueDates: formatDate(responseJson.deadline_time || ""),
      //   description: responseJson.description,
      // });
      return responseJson;
    } else {
      showErrorSnackbar(await extractErrorMessageFromResponse(response));
    }
  };

  const loadSubmissionLink = async () => {
    const responseJson = await get(
      `/student/me/programs/${programId}/modules/${moduleId}/assignments/${assignmentId}/submissions`
    );

    if (response.ok) {
      // const status = responseJson.status || "";

      setAssignment({
        ...assignment,
        link: responseJson.submission_link || "",
        status: status,
        canSubmit: status == "APPROVED" ? false : true,
      });

      return responseJson;
    } else {
      showErrorSnackbar(await extractErrorMessageFromResponse(response));
    }
  };

  const goBack = () => {
    history.goBack();
  };

  // componentDidMount
  useEffect(async () => {
    const assignmentJson = await loadAssignment();
    const submissionJson = await loadSubmissionLink();
    setAssignment(
      createAssignment(
        assignmentJson.id || "",
        assignmentJson.name || "",
        formatDate(assignmentJson.deadline_time || ""),
        assignmentJson.description || "",
        submissionJson.submission_link || "",
        submissionJson.status || "",
        submissionJson.status === "APPROVED" ? false : true
      )
    );
    setLoaded(true);
  }, []);

  const mainContent = (
    <Card>
      <CardHeader color="primary">
        <h4 className={classes.cardTitleWhite}>{assignment.title}</h4>
      </CardHeader>
      <CardBody>
        <Typography>{`Due date: ${assignment.dueDates}`}</Typography>
        <Typography>{`Assignment description: ${assignment.description}`}</Typography>
        <Typography>{`Submission status: ${assignment.status}`}</Typography>
        <Typography>
          <CustomInput
            labelText="Link"
            id="link"
            formControlProps={{
              fullWidth: true,
              required: true,
            }}
            inputProps={{
              name: "link",
              readOnly: assignment.canSubmit ? false : true,
              disableUnderline: assignment.canSubmit ? false : true,
              onChange: handleChange,
              value: assignment.link,
            }}
          />
        </Typography>
        {/* Bottom Buttons */}
        <GridContainer>
          <GridItem xs="auto" sm="auto" md={10} />
          <GridItem xs={12} sm={12} md={2}>
            <CardActions style={{ justifyContent: "flex-end" }}>
              <Button variant="contained" color="secondary" onClick={goBack}>
                Back
              </Button>
              {assignment.canSubmit ? (
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleSubmit}
                >
                  Submit
                </Button>
              ) : null}
            </CardActions>
          </GridItem>
        </GridContainer>
        {/* The snack bar */}
        <Snackbar
          autoHideDuration={6000}
          anchorOrigin={{ vertical: "top", horizontal: "right" }}
          open={snackbarOpen}
          onClose={handleSnackbarClose}
        >
          <Alert onClose={handleSnackbarClose} severity={snackbarSeverity}>
            {snackbarMessage}
          </Alert>
        </Snackbar>
      </CardBody>
    </Card>
  );

  return !loaded ? null : mainContent;
}
